
.health-projects{
  width: 100%;
  display: none;
  overflow: hidden;
}
.project-container{
  width: 100%;
  margin: 0 auto;
}
.project-list{
  width: 100%;
  white-space: nowrap;
  overflow-x: auto;
  padding-bottom: 5px;
  background: linear-gradient(180deg, #7ab9e4, #67a8e4);
}
.project-item{
  width: 90px;
  height: 90px;
  display: inline-block;
  vertical-align: text-bottom;
  border-radius: 15px;
  margin: 5px;
  margin-left: 0;
  transition: all 0.3s linear;
}
.project-item-act{
  background-color: #5b68c1;
}
.project-item .item-icon{
  display: block;
  margin: 10px auto;
  width: 36%;
  padding-bottom: 36%;
}
.project-item .item-title{
  text-align: center;
  margin-top: 5px;
  color: #fff;
  white-space: pre-line;
}
.project-list>.project-item:nth-of-type(1) .item-icon{
  background: url(../assets/group_imgs/bone.png) no-repeat center center;
  background-size: cover;
}
.project-list>.project-item:nth-of-type(2) .item-icon{
  background: url(../assets/group_imgs/child.png) no-repeat center center;
  background-size: cover;
}



