    /*menu*/
aside[data-v-735722ae]{
      position: fixed;
      bottom: -60px;
      right: 6px;
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg);
      -webkit-transform-origin: right top;
              transform-origin: right top;
}
.menu-trigger[data-v-735722ae]{
      display: inline-block;
      line-height: 1;
      white-space: nowrap;
      cursor: pointer;
      border: 1px solid #bfcbd9;
      color: #1f2d3d;
      -webkit-appearance: none;
      text-align: center;
      box-sizing: border-box;
      outline: none;
      margin: 0;
      -moz-user-select: none;
      -webkit-user-select: none;
      -ms-user-select: none;
      padding: 10px 15px;
      font-size: 14px;
      border-radius: 4px;
      background: #eefff5 url(../assets/imgs/menu.svg) no-repeat 7% center;
      background-size: 26px 26px;
      padding-left: 40px;
}
.menu-group[data-v-735722ae]{
      margin-top: 10px;
}
.menu-item[data-v-735722ae]{
      display: block;
      margin-top: 5px;
      background-color: red;
      border-radius: 15px;
      padding: 5px 10px;
      transition: all 3s ease;
      opacity: 0;
}
.jump_in[data-v-735722ae]{
      -webkit-animation: jump 0.9s  linear  alternate forwards;
              animation: jump 0.9s  linear  alternate forwards;
      transition: all 3s;
}
.jump_out[data-v-735722ae]{
      -webkit-animation: jump 0.9s  linear  reverse backwards;
              animation: jump 0.9s  linear  reverse backwards;
      transition: all 3s;
}
@-webkit-keyframes jump {
0%{
        -webkit-transform: translateX(-50px);
                transform: translateX(-50px);
        opacity: 0;
}
50%{
        opacity: 0.5;
        -webkit-transform: translateX(-30px);
                transform: translateX(-30px);
}
100%{
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
}
}
@keyframes jump {
0%{
        -webkit-transform: translateX(-50px);
                transform: translateX(-50px);
        opacity: 0;
}
50%{
        opacity: 0.5;
        -webkit-transform: translateX(-30px);
                transform: translateX(-30px);
}
100%{
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
}
}



