  position: relative;
}
.icon-plus[data-v-903ff54a]{
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: red;
  display: inline-block;
  background: url(../assets/group_imgs/line.png) no-repeat center center;
  background-size: 100%;
}
.icon-plus[data-v-903ff54a]:nth-of-type(1){
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.icon-plus[data-v-903ff54a]:nth-of-type(2){
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.toggle-button[data-v-903ff54a]{
  border: 0;
  outline: 0;
  display: block;
  position: relative;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background-color: rgba(100  , 100, 100, 0.8);
  margin: 10px auto;
}
.close[data-v-903ff54a]{
  -webkit-animation: close 0.5s linear alternate;
          animation: close 0.5s linear alternate;
}
.open[data-v-903ff54a]{
  -webkit-animation: open 0.3s ease-out alternate;
          animation: open 0.3s ease-out alternate;
}
@-webkit-keyframes close {
0%{
    opacity: 1;
    -webkit-transform: rotate(0) scale(1);
            transform: rotate(0) scale(1);
}
100%{
    opacity: 0.2;
    -webkit-transform: rotate(-45deg) scale(0.6);
            transform: rotate(-45deg) scale(0.6);
}
}
@keyframes close {
